import { UserType } from "Redux/users-reducer";

/**
 *              .
 *
 * @param {UserType[]} items -  ,     .
 * @param {number} userId -       .
 * @param {keyof UserType} objPropName -  ,       userId.
 * @param {Partial<UserType>} newObjProps -  ,     ,   .
 *
 * @returns {UserType[]} -      .
 */
export const updateObjectInArray = (
  items: UserType[],
  userId: number,
  objPropName: keyof UserType,
  newObjProps: Partial<UserType>,
): UserType[] => {
  return items.map((u) =>
    u[objPropName] === userId ? { ...u, ...newObjProps } : u,
  );
};
